<template>
  <div class="tab-control">
    <div v-for="(item,index) in titles" :key="item" class="tab-control-item" :class="{active: currentIndex==index}" @click="itemClick(index)">
      <span>{{item}}</span>
    </div>
  </div>
</template>

<script>
    export default {
        name: "TabControl",
      props:{
          titles:{
            type:Array,
            default(){
              return []
            }
          }
      },
      data()
      {
        return {
          currentIndex:0,
        }
      },
      methods:{
        itemClick(index)
        {
          this.currentIndex=index
          this.$emit('tabClick',index)
        }
      }
    }
</script>

<style scoped>
.tab-control{
  display: flex;
  text-align: center;
  height: 40px;
  line-height: 40px;
  font-size: 15px;

}
  .tab-control-item{
    flex: 1;
  }
  .active{
   color: #f9a7a7;
  }
  .active span{
    border-bottom:solid 1px #f9a7a7;
  }
  .tab-control-item span{
    padding: 5px;
  }
</style>
