<template>
  <div  class="detail">
    <detail-bar class="deailBar"></detail-bar>
    <Scroll class="content">
      <detail-swiper :SkuList="SkuList"></detail-swiper>
      <DetailBaseInfo :spu="spu" :skus="SkuList"></DetailBaseInfo>
      <DetailImgInfo :skus="SkuList" :spu="spu"></DetailImgInfo>
      <DetailCommit :skus="SkuList"></DetailCommit>
    </Scroll>
  </div>
</template>

<script>
  import DetailBar from 'components/common/detail/child/DetailBar'
  import DetailSwiper from 'components/common/detail/child/DetailSwiper'
  import DetailBaseInfo from 'components/common/detail/child/DetailBaseInfo'
  import DetailImgInfo from 'components/common/detail/child/DetailImgInfo'
  import DetailCommit from 'components/common/detail/child/DetailCommit'
  import Scroll from 'components/common/scroll/Scroll'

    export default {
        name: "detail",
      components:{
          DetailBar,
        DetailSwiper,
        DetailBaseInfo,
        Scroll,
        DetailImgInfo,
        DetailCommit
      },
      data(){
        return{
          id:null,
          SkuList:null,
          spu:null
        }
      },
      created()
      {
        this.id=this.$route.params.id
        //过去spu信息
        this.getSpu()
        this.$http.get('/item/sku/list',{
          params:{
            id:this.id
          }
        }
      ).then(res=>{
          this.SkuList=res.data
        }).catch(()=>{
        })
      },
      methods:{
          getSpu()
          {
            this.$http.get('/item/item',{
              params:{
                spuId:this.id
              }
              }
            ).then(res=>{
                this.spu=res.data
            })
          }
      }
    }
</script>

<style scoped>
  .deailBar
  {
    position: relative;
    z-index: 1;
    background-color: white;
  }
  .detail
  {
    position: relative;
    z-index: 1;
    height: 100vh;
    background-color: #fff;
  }
  .content
  {
    height: calc(100% - 44px);
    background-color: #fff;
  }
</style>
