<template>
  <div id="home" class="wrapper">
    <div class="home-bar">
      <nav-bar style="background-color: #DDDDDD">
        <div slot="center" ><search></search></div>
        <div slot="right">
          <Dailog></Dailog>
        </div>
      </nav-bar>
    </div>
    <tab-control
      :titles="['笔袋','橡皮','尺']"
      class="fixed"
      @tabClick="tabClick"
      ref="tabControl1"
      v-show="isTop"
    ></tab-control>
    <scroll class="content"
            ref="scroll"
            :probeType="3"
            @scroll="contentScroll"
            :pull-up-load="true"
            @pullingUp="loadMore"
    >
      <div>
        <carousel :msg="banner" @swiperImgLoad="ImgLoad"></carousel>
        <feature-view :features="recommend"></feature-view>
        <recommend-view></recommend-view>
        <tab-control
          :titles="['笔袋','橡皮','尺']"
          class="tab-control"
          @tabClick="tabClick"
          ref="tabControl2"></tab-control>
        <goods-list :goods="goods[currentType].list"></goods-list>
      </div>
    </scroll>
    <back-top @click.native="backTop"  v-show="isShow" class="back-top"></back-top>
  </div>

</template>

<script>
  import Carousel from 'views/home/child/Carousel'
  import RecommendView from 'views/home/child/RecommendView'
  import FeatureView from 'views/home/child/FeatureView'

  import NavBar from 'components/common/navbar/NavBar'
  import Scroll from 'components/common/scroll/Scroll'
  import Search from 'components/common/search/Search'
  import BackTop from 'components/common/backtop/BackTop'

  import TabControl from 'components/content/tabControl/TabControl'
  import GoodsList from 'components/content/goods/GoodsList'
  import Dailog from 'components/content/dailog/Dailog'


  import {getHomeMultidata,getHomeData} from 'network/home'
    export default {
        name: "home",
      components:{
          NavBar,
        Carousel,
        RecommendView,
        FeatureView,
        TabControl,
        GoodsList,
        Scroll,
        BackTop,
        Search,
        Dailog
      },
      data(){
          return{
            saveY:0,
            isTop:false,
            tabOffsetTop:0,
            isShow:false,
            banner:[],
            recommend:[],
            currentType:'笔袋',
            goods:{
              '笔袋':{page:0,list:[]},
              '橡皮':{page:0,list:[]},
              '尺':{page:0,list:[]}
            }
          }
      },
      created(){
          // this.$http.get('api/spu/detail/1').then(res=>{
          //   console.log(res)
          // })
        this.getHomeMultidata()
        this.getHomeData('笔袋');
        this.getHomeData('橡皮');
        this.getHomeData('尺');
      },
      mounted()
      {
        this.$bus.$on('loadRefresh',()=>{
          this.$refs.scroll.refresh()
        })
      },
      activated(){
          this.$refs.scroll.scrollTo(0,this.saveY,0)
          this.$refs.scroll.refresh()
      },
      deactivated(){
          this.saveY=this.$refs.scroll.getY()
      },
      methods:{
        ImgLoad()
        {
          this.tabOffsetTop=this.$refs.tabControl2.$el.offsetTop
        },
        loadMore(){
          this.getHomeData(this.currentType)
        },
        contentScroll(position)
        {
          this.isShow=(-position.y)>1000
          this.isTop=(-position.y)>this.tabOffsetTop
        },
        backTop()
        {
          this.$refs.scroll.scrollTo(0,0)
        },
        tabClick(index){
            switch (index) {
              case 0:
                this.currentType="笔袋"
                    break;
              case 1:
                this.currentType="橡皮"
                break;
              case 2:
                this.currentType="尺"
                break;
            }
            this.$refs.tabControl1.currentIndex=index
            this.$refs.tabControl2.currentIndex=index
          },
        getHomeData(type){
          const page=this.goods[type].page+1;
          getHomeData(type,page).then(res=>{
            // console.log(res)
                this.goods[type].list.push(...res.items)
                this.goods[type].page+=1
                this.$refs.scroll.finishPullUp()
          }).catch(()=>{

            }
          )
        },
        getHomeMultidata(){
          getHomeMultidata().then(res=>{
            this.banner=res.data.banner.list
            this.recommend=res.data.recommend.list
          })
        }
      }
    }
</script>

<style scoped>
  #home{
    padding-top: 44px;
    position: relative;
    height: 100vh;
  }
  .content{
    position: absolute;
    overflow: hidden;

    top: 44px;
    bottom: 49px;
    right: 0;
    left: 0;
  }
.home-bar{
  background-color: #f9a7a7;
  color: #fff;
  position: fixed;
  left: 0;
  right: 0;
  top:0;
  z-index: 9;
}
  .tab-control{
    position: sticky;
    top: 44px;
    z-index: 9;
    background-color: white;
  }
  .back-top {
    position: fixed;
    bottom: 60px;
    right: 10px;
  }
  .fixed {
    position: fixed;
    top: 44px;
    left: 0;
    right: 0;
  }
</style>
