
import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../views/home/home'
import category from '../views/category/category'
import my from '../views/my/my'
import cart from '../views/cart/cart'
import detail from 'components/common/detail/detail'
import SearchDetail from 'components/common/search/SearchDetail'

Vue.use(VueRouter)

const routes = [
    {
        path:'/',
        redirect:'/home'
    },
  {
    path: '/home',
    name: 'home',
    component: home
  },
    {
        path:'/cart',
        component:cart,
        name:'cart'
    },
    {
        path:'/category',
        component:category,
        name:'category'
    },
    {
        path:'/my',
        component:my,
        name:'my'
    },
  {
    path:'/detail/:id',
    component:detail,
    name:'detail'
  },
  {
    path:'/SearchDetail',
    component:SearchDetail,
    name:"SearchDetail"
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
