<template>
  <div class="b">
    <!--<el-button type="text" @click="DialogVisible = true" class="el-button"><img src="~assets/store.png"/></el-button>-->

      <img src="~assets/store.png" class="a" @click="DialogVisible = true"/>

    <el-dialog
      title="商家入驻"
      :visible.sync="DialogVisible"
      width="75%"
      @close=" dailogClose"
      class="dailog"
      center>
      <el-form ref="FormRef" :model="storeForm" label-width="80px" :rules="dailogFormRules">
        <el-form-item label="商铺名称" prop="name">
          <el-input v-model="storeForm.name"></el-input>
        </el-form-item>
        <el-form-item label="商铺地址" prop="address">
          <el-select v-model="storeForm.address" placeholder="请选择商铺地址">
            <el-option label="3舍" value="shanghai"></el-option>
            <el-option label="2舍" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商铺类型" prop="type">
          <el-checkbox-group v-model="storeForm.type">
            <el-checkbox label="零食铺子" name="snack"></el-checkbox>
            <el-checkbox label="杂货铺子" name="goods"></el-checkbox>
            <el-checkbox label="有偿代理" name="help"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="商铺描述" prop="desc">
          <el-input type="textarea" v-model="storeForm.desc"></el-input>
        </el-form-item>
        <el-form-item label="商铺logo" prop="logo">
            <load-img @LoadImg="loadImg"></load-img>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="DialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="onSubmit">立即申请</el-button>
    </span>
    </el-dialog>
  </div>
</template>

<script>
  import LoadImg from 'other/LoadImg'
    export default {
        name: "Dailog",
      components:{
        LoadImg
      },
      data(){
          return{
            DialogVisible:false,
            storeForm: {
              name: '',
              type: '',
              address: '',
              desc: '',
              imgUrl: ''
            },
              dailogFormRules:{
                name:[
                  { required: true, message: '请输入商铺名', trigger: 'blur' },
                  { min: 1, max: 6, message: '长度在 1到 6 个字符', trigger: 'blur' }
                ],
                address:[
                  { required: true, message: '请输入商铺地址', trigger: 'blur' },
                ],
                type:[
                  { required: true, message: '请选择商铺类型', trigger: 'blur' },
                ],
                desc:[
                  { required: true, message: '请添加商铺描述', trigger: 'blur' },
                  { min: 2, max: 30, message: '长度在 2到 30 个字符', trigger: 'blur' }
                ],
                logo:[
                  { required: true, message: '请上传商铺logo', trigger: 'blur' },
                ],
              }
            }

      },
      methods: {
        //对话框关闭时清除数据
        dailogClose(){
          this.$refs.FormRef.resetFields()
        },
        //店铺入驻申请
        onSubmit() {
          this.$refs.FormRef.validate(valid => {
              if(valid)
              {
                this.DialogVisible = !this.DialogVisible
                this.$message.success("申请成功，等待审核！")
                console.log("申请成功")
              }
          })
        },
        //获取图片地址
        loadImg(url){
          this.storeForm.imgUrl=url
       }
      }
    }
</script>

