<template>
  <div>
    <el-upload
      v-show="imageUrl.length < 1"
      class="avatar-uploader"
      action="https://jsonplaceholder.typicode.com/posts/"
      :show-file-list="false"
      :on-success="handleAvatarSuccess"
      :before-upload="beforeAvatarUpload"
      :on-progress="uploadProcess">
      <img v-if="imageUrl" :src="imageUrl" class="avatar">
      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      <el-progress  v-show="imgFlag == true" :percentage="percent" status="success"></el-progress>
    </el-upload>

    <div class="image-preview" v-show="imageUrl.length > 1">
      <div class="image-preview-wrapper">
        <img :src="imageUrl" class="a">
        <div class="image-preview-action">
          <i @click="handleRemove" class="el-icon-delete"></i>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
    export default {
        name: "LoadImg",
      data()
      {
        return{
          imgFlag: false,
          percent: 0,
          imageUrl:''
        }
      },
      methods:{
        //商铺logo上传
        handleAvatarSuccess(res, file)
        {
          this.imgFlag=false
          this.percent=0
          if(res)
          {
            this.imageUrl = URL.createObjectURL(file.raw);
            this.$message.success("上传成功!")
            this.$emit('LoadImg',this.imageUrl)
          }else {
            this.$message.error('视频上传失败，请重新上传！');
          }
        },
        //上传验证
        beforeAvatarUpload(file)
        {
          const isLt10M = file.size / 1024 / 1024  < 2;
          if (
            ['image/jpeg',
              'image/gif',
              'image/png',
              'image/bmp'
            ].indexOf(file.type) == -1) {
            this.$message.error('请上传正确的图片格式');
            return false;
          }
          if (!isLt10M) {
            this.$message.error('上传图片不能超过2MB哦!');
            return false;
          }
          // const isJPG = file.type === 'image/jpeg';
          // const isLt2M = file.size / 1024 / 1024 < 2;
          //
          // if (!isJPG) {
          //   this.$message.error('上传头像图片只能是 JPG 格式!');
          // }
          // if (!isLt2M) {
          //   this.$message.error('上传头像图片大小不能超过 2MB!');
          // }
          // return isJPG && isLt2M;
        },
        uploadProcess(event)
        {
          this.imgFlag = true;
          console.log(event.percent);
          this.percent = Math.floor(event.percent);
        },
        //移除照片
        handleRemove()
        {
          this.imageUrl=''
        }
      }
    }
</script>

<style scoped>
  /*上传图片*/
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 160px;
    height: 160px;
    line-height: 160px;
    text-align: center;
  }
  .avatar{
    width: 160px;
    height: 160px;
    display: block;
  }
  .a{
    width: 160px;
    height: 160px;
    display: block;
  }
</style>
