<template>
   <div class="tab-bar-item" @click="checkActive">
      <div v-if="!isActive"><slot name="item-img"></slot></div>
       <div v-else><slot name="item-img-active"></slot></div>
        <div :class="{active: isActive}"><slot name="item-text"></slot></div>
   </div>
</template>

<script>
    export default {
        name: "TabBarItem",
        props:{
            path:String
       },
        date(){
            return{
                // isActive:false
            }
        },
        computed:{
            isActive()
            {
                return this.$route.path.indexOf(this.path) !==-1
            }
        },
        methods:{
            checkActive()
            {
                this.$router.push(this.path)

            }
        }

    }
</script>

<style scoped>
  .tab-bar-item{
    text-align: center;
    flex: 1;
    height: 49px;
    font-size: 14px;
  }
  .tab-bar-item img{
    height: 24px;
    width: 24px;
  }
  .active{
    color: red;
  }
</style>
